* {
  box-sizing: inherit;
}

html,
body {
  font-size: 10px;
  
}

body {
  font-family: 'roboto', sans-serif;
  margin: 0;
  background-color: var(--light);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'poppins', sans-serif;
  color: var(--dark);
  margin-top: 0;
  hyphens: none;
}

h4, h5, h6 {
  color: var(--primary-color);
}

h1 {
  margin-bottom: 3rem;
  font-size: 5em;
  line-height: 6rem;
}

h2 {
  margin-bottom: 3rem;
  font-size: 3em;
  line-height: 4rem;
  text-align: center
}

h3 {
  margin-bottom: 3rem;
  font-size: 2.5em;
  line-height: 3.5rem;
}

h4 {
  margin-bottom: 2rem;
  font-size: 2em;
  line-height: 3rem;
}

h5 {
  margin-bottom: 2rem;
  font-size: 1.75em;
  line-height: 2.75rem;
}

h6 {
  margin-bottom: 2rem;
  font-size: 1.4em;
  line-height: 2.25rem;
}

p, li {
  margin-bottom: 2rem;
  font-size: 1.4em;
  line-height: 2.25rem;
}

ul {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
}

li {
  position: relative;
  display: block;
  padding: 1.25rem 0;
  margin-bottom: -1px;
  border-bottom: 1px solid rgba(51,49,59,.125);
}

small {
  font-size: .5em;
}

a {
  color: var(--secondary-color);
}

// /* header and footer areas */
.rancher-header, 
.sub-header, 
footer {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rancher-header {
  background:var(--dark);
  
  a {
    color: white;
  }

  div {
    display: flex;
    align-items: center;
  
    > * {
      margin: 0 5px;
    }
  }
}

.sub-header {
  background: white;
  
  a img {
    height: 40px;
  }

