}

footer {
  background: var(--dark);
  padding: 2rem 1rem;

  p, a {
    color: var(--light);
    margin: 0;
    font-size: 12px;
  }
}

hr {
  width: 75%;
  max-width: 1200px;
  border: 0;
  height: 1px;
  background-image: -webkit-linear-gradient(left, #F3F3F3, #adadb1, #F3F3F3);
  background-image: -moz-linear-gradient(left, #F3F3F3, #adadb1, #F3F3F3);
  background-image: -ms-linear-gradient(left, #F3F3F3, #adadb1, #F3F3F3);
  background-image: -o-linear-gradient(left, #F3F3F3, #adadb1, #F3F3F3);
}

pre {
  background: var(--dark);
  color: var(--light);
  page-break-inside: avoid;
  font-family: monospace;
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1em 1.5em;
  display: block;
  word-wrap: break-word;
  border-radius: 3px;
  white-space: pre-wrap;
}

/* elements */
img, iframe, video {
  max-width: 100%;
}

main {
  hyphens: auto;
}

blockquote {
  background: #f9f9f9;
  border-left: 5px solid #ccc;
  padding: 3px 1em 3px;
}

table {
  margin: auto;
  border-top: 1px solid #BFBFBF;
  border-bottom: 1px solid #BFBFBF;
  thead th {
    border-bottom: 1px solid #ddd;
  }
}

th, td {
  padding: 5px;
}

thead, tfoot, tr:nth-child(even) {
  background: #eee;
}


a.btn {
  font-family: 'poppins', sans-serif;
  text-transform: uppercase;
  background: var(--secondary-color);
  color: var(--light)!important;
  padding: 10px 20px;
  text-decoration: none;
  font-size: 1.25rem;
  line-height: 0;
  font-weight: bold;
  align-items: center;
  border-radius: 3px;
  letter-spacing: 1px;

  > img {
    padding-right: .5rem;
    height: 20px;
  }

  &.no-bg {
    background: transparent;
    color: var(--secondary-color)!important;
  }
}

//layout
section {
  padding: 10rem 0;
}

.wrap {
  box-sizing: border-box;
  width: 75%;
  max-width: 1200px;
  margin: 0 auto;
}

.intro {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  align-items: end;
  column-gap: 2rem;
  position: relative;

  .grid-two {
    align-items: center;
  }

  // :after {
