}

//accents
.bg-primary {
  background-color: var(--primary-color);
  h1, h2, h3, h4, h5, h6, p, a {
    color: var(--dark);
  }
}

.bg-secondary {
  background-color: var(--secondary-color);
  h1, h2, h3, h4, h5, h6, p, a {
    color: var(--light);
  }
}

.bg-dark {
  background-color: var(--dark);
  h1, h2, h3, h4, h5, h6, p, a {
    color: var(--light);
  }
}

.bg-light {
  background-color: var(--light);
  h1, h2, h3, h4, h5, h6, p, a {
    color: var(--dark);
  }
}

.folder {
  h4 {
    display: inline-block;
    margin: 0;
    padding: 10px 20px;
    background-color: var(--secondary-color);
    color: var(--light);
    position: relative;
  }

  .bg-light {
    background-color: white;
    padding: 40px;
    border-top: solid var(--secondary-color) 14px;
    box-shadow: rgba(51,49,59, 0.5) 0 0 20px;
  }

  .grid-three {

    h5 {
      margin: 0;
      padding: 10px;
      border-radius: 6px;
    }
  }
}


//docs layout
.docs {
  .container {
    display: grid;

    grid-template-areas:
      "header header header"
      "nav content side"
      "footer footer footer";

    grid-template-columns: 300px minmax(50ch, 80ch) auto;
    grid-template-rows: auto 1fr;
    column-gap: 40px;

    height: 100vh;
  }

  header {
    grid-area: header;
  }

  .tree-nav {
    grid-area: nav;
    align-items: start;
    padding: 2rem;
    background-color: var(--primary-color);

    > ul ul {
      display: none
    }

    ul {
      list-style-type: none;
      padding: 0;
      margin: 0;

      li {
        cursor: pointer;

        &.open {
          > img {
            -webkit-transform: rotate(90deg);
            -moz-transform: rotate(90deg);
            -o-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
            transform: rotate(90deg);
          }

          > ul {
            display: block;
          }
        }

        a {
          font-size: 14px;
          font-weight: bold;
          text-decoration: none;
          color: var(--primary-color);
        }

        ul {
          margin-left: 10px;
        }
      }
    }
  }

  main {
    grid-area: content;
    margin-top: 2rem;
  }

  aside {
    grid-area: side;
    margin-top: 2rem;
  }

  footer {
    grid-area: footer;
  }

  //table of contents
  #TableOfContents {
    > ul {
      padding: 0;
      margin: 0;
    }

    ul {
      list-style-type: none;

      li {
        a {
          text-decoration: none;
          padding: .25rem 0;
          display: block;
        }
      }
    }
  }
}

//intro
.intro {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  padding: 0;

  .grid-two {
    grid-template-columns: 60% auto;
  }

  .wrap {
    padding: 5rem 0 15rem 0;
  }

  .folder {
    grid-row: 5 / span 3;
    grid-column: 2 / span 4;
    z-index: 2;
  }

  .bg-primary {
    grid-row: 2 / span 4;
    grid-column: 1 / span 6;
    color: var(--dark);
  }
}

//get started
.get-started {
  display: grid;
  grid-template-columns: repeat(6,1fr);
  padding: 0;
  position: relative;

  .white {
    background: var(--light);
    grid-row: 2 / span 4;
    grid-column: 2 / span 4;
    z-index: 2;
    box-shadow: rgba(51,49,59, 0.5) 0 0 20px;
    padding: 5rem;
  }

  .bg-primary {
    background: var(--primary-color);
    grid-row: 5 / span 3;
    grid-column: 1 / span 6;
    color: white;
    padding: 20rem 0 5rem 0;
  }
}

//responsive
.video-responsive{
    overflow:hidden;
    padding-bottom:56.25%;
    position:relative;
    height:0;
}
.video-responsive iframe{
    left:0;
    top:0;
    height:100%;
    width:100%;
    position:absolute;
}

@media (max-width: 768px) {
  .container {
    grid-template-areas:
      "header"
      "nav"
      "content"
      "side"
      "footer";

    grid-template-columns: 1fr;
    grid-template-rows:
      auto /* Header */
      minmax(75px, auto) /* side Nav */
      1fr /* Content */
      minmax(75px, auto) /* toc */
      auto; /* Footer */
  }

  .wrap {
    width: 90%;
  }

  section {
    padding: 2em;
  }

  nav.main-nav {
    .logo, .logo img {
      height: 35px;
      width: 112px;
      display: inline-block;
    }
  }

  .grid-dynamic {
    display: block;
    text-align: left;

    h5 {
      border-bottom: solid var(--light) 1px;
      padding: 5px 10px;
    }
  }

  .grid-two,
  .grid-three,
  .intro .grid-two {
    grid-template-columns: 1fr;
  }

  .grid-two {
    grid-row-gap: 0;
  }

  .grid-four {
    grid-template-columns: repeat(2, 1fr);
  }

  .hidden-small {
    display: none;
  }

  //intro
  h1 {
    font-size: 4em;
    line-height: 5rem;
  }

  .grid-two {
    grid-gap: 30px;
  }

  .intro .folder {
    grid-row: 5/span 4;
  }

  //get started
  .get-started {
    grid-template-columns: repeat(8,1fr);

    .white {
      // grid-row: 3/span 4;
      grid-column: 2/span 6;
    }
    .bg-primary {
      grid-column: 1/span 8;
    }
  }
}

/*menu test*/
/* Screen reader only */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

/* Base styles for all devices sizes, but mobile first */
/* Hamburger menu icon/'button' styling */
.menu-toggle {
  display: inline-block;
  padding: .75em 15px;
  line-height: 1em;
  font-size: 1em;
  color: var(--dark);
}

.menu-toggle:hover,
.menu-toggle:focus {
  color: var(--primary-color);
}

/* Offscreen menu styling */
.main-menu {
  position: absolute;
  display: none;
  left: -200px; /* push off the screen to the left for future animation */
  top: 0;
  height: 100%;
	overflow-y: scroll;
	overflow-x: visible;
	transition: left 0.3s ease,
				      box-shadow 0.3s ease;
	z-index: 999;
}

.main-menu ul {
  list-style: none;
  margin: 0;
  padding: 2.5em 0 0;
  /* Hide shadow w/ -8px while 'closed' */
  -webkit-box-shadow: -8px 0 8px rgba(0,0,0,.5);
     -moz-box-shadow: -8px 0 8px rgba(0,0,0,.5);
          box-shadow: -8px 0 8px rgba(0,0,0,.5);
  min-height: 100%;
  width: 200px;
  background: var(--dark);
}

/* Generic style for ALL hamburger menu links */
.main-menu a {
  display: block;
  padding: .75em 15px;
  line-height: 1em;
  font-size: 1em;
  color: var(--light);
  text-decoration: none;
  border-bottom: 1px solid var(--dark);
}

.main-menu a.menu-close {
  border-bottom: 0; /* no border for the X 'button' */
}

.main-menu li:first-child a {
  border-top: 1px solid var(--dark); /* add top border only for the first link */
}

/* Hover & focus style */
.main-menu a:hover,
.main-menu a:focus {
  background: var(--dark);
  text-decoration: underline;
}

/* Close button positioning */
.main-menu .menu-close {
  position: absolute;
  right: 0;
  top: 0;
}

/* On small devices, allow it to toggle...*/
/* Opening hamburger menu! */

.main-menu:target,
.main-menu[aria-expanded="true"] {
  display: block;
  left: 0; /* slide from -200px to the left edge at 0 */
  outline: none;
  /* fix box-shadow so it shows now */
  -moz-box-shadow: 3px 0 12px rgba(0,0,0,.25);
  -webkit-box-shadow: 3px 0 12px rgba(0,0,0,.25);
  box-shadow: 3px 0 12px rgba(0,0,0,.25);
}

/* Close menu button */
.main-menu:target .menu-close,
.main-menu[aria-expanded="true"] .menu-close {
  z-index: 1001;
}

/* Hamburger menu list */
.main-menu:target ul,
.main-menu[aria-expanded="true"] ul {
  position: relative;
  z-index: 1000;
}

/* Backdrop */
.main-menu:target + .backdrop,
.main-menu[aria-expanded="true"] + .backdrop{
  position: absolute;
  display: block;  
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  background: var(--dark);
  background: rgba(0,0,0,.85);
  cursor: default;
}

/*  If CSS supports `fixed`, like all good modern browsers */
@supports (position: fixed) {
  /* Allow backdrop to stay fixed and not "scroll" */
  .main-menu,
  .main-menu:target + .backdrop,
  .main-menu[aria-expanded="true"] + .backdrop {
    position: fixed;
  }
}

/*  Larger screen styling */
/* Horizontal menu (i.e. no hamburger) */

@media (min-width: 768px) {
  /* Hide hamburger menu/'button' */
  .menu-toggle,
  .main-menu .menu-close {
    display: none;
  }
  
  /* Undo positioning of off-canvas menu */
  .main-menu {
    position: relative;
		left: auto;
		top: auto;
		height: auto;
    display: block;
    overflow: hidden;
  }
  
  .main-menu ul {
    display: flex; /* allows for full height of menu */
    
    /* Undo off-canvas styling */
    -webkit-box-shadow: none;
       -moz-box-shadow: none;
            box-shadow: none;
    height: auto;
    width: auto;
    background: none;
        padding: 0;
    margin: 0;
    flex-direction: row;
    align-items: center;

    li {
      display: inline-block;
      padding: 0;
      margin: 0;
      border: none;
  
      &:last-child {
        padding-right: 0;
      }
    }

    a.btn {
      display: inline-flex;
    }
  }

  .main-menu a {
    color: var(--secondary-color);
    border: 0 !important; /* Remove borders from off-canvas styling */
  }
  
  .main-menu a:hover,
  .main-menu a:focus {
    background: none; /* Remove background from off-canvas styling */
    color: var(--primary-color);
  }
}
